<template>
  <section class="theater">
    <template v-if="!$root.isMobile">
      <div class="theater__inner container">
        <div class="theater__main-info">
          <div class="theater__text-info">
            <h3>{{ title }}</h3>
            <div class="theater__texts">
              <p v-for="(text, i) in texts" :key="i">{{ text.text }}</p>
            </div>
          </div>
          <div class="theater__pictures">
